// frontend/src/app/page.js
'use client';
import { useState } from 'react';

export default function Home() {
  const [file, setFile] = useState(null);
  const [duration, setDuration] = useState(30);
  const [ratio, setRatio] = useState('1');
  const [language, setLanguage] = useState('1');
  const [loading, setLoading] = useState(false);
  const [videoUrl, setVideoUrl] = useState(null);
  const [status, setStatus] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!file) return alert("Pilih video dulu bos!");

    setLoading(true);
    setVideoUrl(null);
    setStatus("Sedang upload & memproses AI... Mohon tunggu.");

    const formData = new FormData();
    formData.append('file', file);
    formData.append('duration', duration);
    formData.append('ratio', ratio);
    formData.append('language', language);

    try {
      const response = await fetch('http://localhost:8000/process-video', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) throw new Error("Gagal proses di server");

      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      setVideoUrl(url);
      setStatus("Selesai! Silakan download.");
    } catch (error) {
      alert("Error: " + error.message);
      setStatus("Gagal.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-900 text-white flex flex-col items-center py-10 font-sans">
      <h1 className="text-4xl font-bold mb-8 text-yellow-400">üé¨ AI Video Editor Bramasta</h1>
      
      <div className="bg-gray-800 p-8 rounded-xl shadow-lg w-full max-w-md border border-gray-700">
        <form onSubmit={handleSubmit} className="space-y-5">
          
          <div>
            <label className="block mb-2 font-bold text-gray-300">1. Upload Video</label>
            <input 
              type="file" 
              accept="video/*"
              onChange={(e) => setFile(e.target.files[0])}
              className="block w-full text-sm text-gray-400 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-yellow-500 file:text-gray-900 hover:file:bg-yellow-400"
            />
          </div>

          <div>
            <label className="block mb-2 font-bold text-gray-300">2. Durasi Potongan (Detik)</label>
            <input 
              type="number" 
              value={duration} 
              onChange={(e) => setDuration(e.target.value)}
              className="w-full p-3 rounded bg-gray-700 border border-gray-600 focus:border-yellow-500 outline-none text-white"
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block mb-2 font-bold text-gray-300">3. Rasio</label>
              <select 
                value={ratio} 
                onChange={(e) => setRatio(e.target.value)}
                className="w-full p-3 rounded bg-gray-700 border border-gray-600 focus:border-yellow-500 outline-none"
              >
                <option value="1">Original</option>
                <option value="2">TikTok (9:16)</option>
              </select>
            </div>
            <div>
              <label className="block mb-2 font-bold text-gray-300">4. Bahasa</label>
              <select 
                value={language} 
                onChange={(e) => setLanguage(e.target.value)}
                className="w-full p-3 rounded bg-gray-700 border border-gray-600 focus:border-yellow-500 outline-none"
              >
                <option value="1">Auto</option>
                <option value="2">Indonesia</option>
                <option value="3">Inggris</option>
              </select>
            </div>
          </div>

          <button 
            type="submit" 
            disabled={loading}
            className={`w-full py-4 rounded-lg font-bold text-lg transition shadow-lg ${loading ? 'bg-gray-600 cursor-not-allowed' : 'bg-gradient-to-r from-yellow-500 to-orange-500 hover:from-yellow-400 hover:to-orange-400 text-gray-900'}`}
          >
            {loading ? '‚è≥ Sedang Memproses...' : 'üöÄ Mulai Magic'}
          </button>
          
          {status && <p className="text-center text-sm text-gray-400 mt-2">{status}</p>}
        </form>
      </div>

      {videoUrl && (
        <div className="mt-10 w-full max-w-md">
          <h2 className="text-2xl font-bold mb-4 text-center text-green-400">‚ú® Hasil Video Jadi!</h2>
          <video controls src={videoUrl} className="w-full rounded-lg shadow-2xl border-2 border-green-500" />
          <a 
            href={videoUrl} 
            download="hasil_video_ai.mp4" 
            className="block mt-4 text-center bg-green-600 py-3 rounded-lg font-bold hover:bg-green-500 transition shadow-lg"
          >
            ‚¨áÔ∏è Download Video
          </a>
        </div>
      )}
    </div>
  );
}